import { useState } from "react";
import Login from "../../../Modal/Login";

import s from "./SalonDescription.module.scss";

const SalonDescription = () => {
  const [isOpen, setOpen] = useState(false);

  const showMenu = (e) => {
    if (!isOpen) setOpen(true);
    if (e.target.nodeName === "DIV") setOpen(false);
  };
  return (
    <div className={s.salonDescription}>
      <h2 className={s.salonDescription__title}>
        <span className={s.accent} onClick={showMenu}>
          Salon
        </span>
        kosmetyczny
      </h2>
      {isOpen && <Login toggle={showMenu} />}
      <p className={s.salonDescription__text}>
        Szanowni Pastwo! Witamy serdecznie w naszym nowoczesnym
        <span className={s.accent}> Kiwi beauty center.</span> Pikne wntrze,
        przytulna atmosfera, obsuga i troskliwe rce profesjonalistw zadowol
        najbardziej wymagajcego klienta.
      </p>
      <p className={s.salonDescription__text}>
        Zespl <span className={s.accent}>Kiwi beauty center</span>, to
        wyszkolone kosmetyczki i kosmetolodzy ktre maj gust i wyczucie stylu
        aby stworzy twj obraz. Naszym priorytetem jest sumienno, wysoka
        jako i profesjonalizm,majcy zapenisz najwyszy poziom wiadczonych
        przez nas usug.
      </p>
    </div>
  );
};

export default SalonDescription;
